.advancedOptionsButton {
    font-size: 2em;
    border: 1px solid var(--clr-txt);
    border-radius: 5px
}

.advancedOptionsButton.inUse {
    box-shadow: 0px 0px 10px goldenrod;
}

.advancedOptionsButton:hover {
    cursor: pointer;
    filter: brightness(0.8);
}

.advancedOptionsButton:active {
    filter: brightness(0.7);
}

.advancedOptionsModal {
    text-align: left;
    background-color: rgb(70, 70, 70);
    border-radius: 10px;
    line-height: 1.4285em;
    padding: 20px;
}

.advancedOptionsModal .closeModalButton {
    width: 100%;
    height: 2.5em;
}

.advancedOptionsSection {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    margin-bottom: 15px;
    align-items: flex-start;
}

.advancedOptionsSection .rarityLabel {
    margin-bottom: 15px;
}

.advancedOptionsSection .colorBox{
    padding: 0;
}

.advancedOptionsSection .colorList {
    padding-top: 0;;
}

.advancedOptionsSection .reset-button {
    height: 35px;
    align-self: flex-end;
}
